@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  * {
    @apply scroll-smooth;
  }
  
  body {
    @apply bg-gradient-to-br from-gray-50 via-white to-gray-50 text-primary antialiased;
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', sans-serif;
  }

  html {
    scroll-behavior: smooth;
  }
}

@layer components {
  .btn-primary {
    @apply relative bg-gradient-to-r from-accent to-cyan-500 text-white px-8 py-3.5 rounded-xl font-semibold 
           shadow-lg shadow-accent/30 hover:shadow-xl hover:shadow-accent/40 
           transform hover:scale-105 active:scale-95 
           transition-all duration-300 ease-out
           focus:outline-none focus:ring-2 focus:ring-accent focus:ring-offset-2
           overflow-hidden;
  }

  .btn-primary.group::before,
  .group .btn-primary::before {
    content: '';
    @apply absolute inset-0 bg-gradient-to-r from-cyan-500 to-accent opacity-0 transition-opacity duration-300;
  }

  .btn-primary.group:hover::before,
  .group:hover .btn-primary::before {
    @apply opacity-100;
  }

  .btn-primary span {
    @apply relative z-10;
  }

  .btn-secondary {
    @apply bg-white/10 backdrop-blur-sm border-2 border-white/30 text-white px-8 py-3.5 rounded-xl font-semibold 
           hover:bg-white/20 hover:border-white/50 
           transform hover:scale-105 active:scale-95
           transition-all duration-300 ease-out
           focus:outline-none focus:ring-2 focus:ring-white focus:ring-offset-2 focus:ring-offset-primary;
  }

  .card {
    @apply bg-white rounded-2xl shadow-lg hover:shadow-2xl 
           transform hover:-translate-y-2 
           transition-all duration-300 ease-out
           border border-gray-100;
  }

  .card-hover {
    @apply card hover:border-accent/20;
  }

  .gradient-text {
    @apply bg-gradient-to-r from-accent via-cyan-500 to-purple-500 bg-clip-text text-transparent;
  }

  .glass {
    @apply bg-white/80 backdrop-blur-lg border border-white/20;
  }

  .skeleton {
    @apply animate-pulse bg-gray-200 rounded;
  }

  .shimmer {
    @apply relative overflow-hidden;
  }

  .shimmer::after {
    content: '';
    @apply absolute inset-0 -translate-x-full animate-shimmer bg-gradient-to-r from-transparent via-white/20 to-transparent;
  }
}

